}
</script>

<style scoped>
.order-title{font-size: .36rem;}
.order-num{margin: .1rem 0;}
.mt-20{margin-top: .2rem;}
.flex-between{display: flex;align-items: center;justify-content: space-between;}
.arrow{width: .11rem;height: .2rem;}
.address{color: #666;margin-top: .1rem;}
.msg div:first-child{width: 2rem;}
.msg div:last-child{flex: 1;}
.pad-bottom{padding-bottom: 1rem;}
.fixed-bottom div:nth-of-type(1){background: #29A1F7;color: #fff;}
 .fixed-bottom div:nth-of-type(2){background: #4DC065;color: #fff;}
</style>


